<template>
    <h2>&lt;-&nbsp;&nbsp;&nbsp;好物推荐&nbsp;&nbsp;&nbsp;-&gt;</h2>
    <div class="warp">
        <div class="content_item"
            v-for="(item,index) in content_item"
            :key="index"
            @click="getPath(item)"
        >
            <img :src="item.content_img" alt="商品图片">
            <p class="content_text">{{item.content_text}}</p>
            <span><em>￥</em>&nbsp;{{item.price}}&nbsp; <i>{{item.payprice}}人已购买</i></span>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    h2{
        font-family: '楷体';
        text-align: center;
        color: rgba(0, 0, 0, .7);
        margin-bottom: .9375rem;
    }
   .warp{
       display: flex;
       flex-wrap: wrap;
       .content_item{
           width: 45%;
           box-shadow: 1px 1px 8px #cfcfcf;
           border-radius: .3125rem;
           overflow: hidden;
           margin-left: .9375rem;
           margin-bottom: 1.25rem;
           img{
               width: 100%;
               height: 11.5625rem;
           }
           .content_text{
               width: 90%;
               margin: .625rem;
               margin-right: 0;
               margin-top: .3125rem;
               overflow: hidden;
               text-overflow: ellipsis;
               display: -webkit-box;
               -webkit-box-orient: vertical;
               -webkit-line-clamp: 2;
           }
           span{
               display: block;
               width: 90%;
               margin: .625rem;
               margin-right: 0;
               margin-top: .3125rem;
               color: red;
               font-size: 1.125rem;
               em{
                   color: red;
                   font-size: .75rem;
               }
               i{
                   color: rgba(0, 0, 0, .5);
                   font-size: .75rem;
               }
           }
       }
        // text-align: center;
    }
</style>

<script>
import { get } from '../../untils/request'
export default {
    name: 'Choiceness',
    data () {
        return {
            content_item: []
        }
    },
    mounted () {
        this.getContentItem()
        this.getCommodityDetails()
    },
    methods: {
        getContentItem: async function () {
            try {
                const result = await get('/choiceness_content')
                if (result.code === 200 && result.content_item.length) {
                    this.content_item = result.content_item
                    console.log('请求成功')
                }
            } catch (e) {
                console.log('请求失败!')
            }
        },
        getCommodityDetails: async function () {
            // console.log(this.$store.state.commodityContent)
            // console.log(this.$route.params.id)
            const result = await get('/choiceness_content')
            this.$store.state.commodityContent = result.content_item
            for (const item in this.$store.state.commodityContent) {
                // console.log(Number(this.$route.params.id))
                // console.log(this.$store.state.commodityContent[item])
                if (this.$store.state.commodityContent[item].id === Number(this.$route.params.id)) {
                    // console.log(this.$store.state.commodityContent[item])
                    this.$store.commit('getCommodityDetails', this.$store.state.commodityContent[item])
                }
            }
        },
        getPath: function (item) {
            if (localStorage.isLogin) {
                this.$router.push(`/commodity_details/${item.id}`)
            } else {
                this.$router.push('/login')
            }
        }
    }
}
</script>
